.navBar {
  margin: 0;
  padding: 0;
}
.navBarMenu {
  position: fixed;
  padding: 100px 0 0 0;
  display: flex;
  flex-direction: column;
  a {
    margin: 0;
    padding: 0 0 30px 0;
    opacity: 50%;
    mix-blend-mode: difference;
  }
}
.focus {
  opacity: 100% !important;
}
.lightFont {
  font-weight: 100;
}
